body {
  height: 100vh;
}

#root {
  height: 100%;
}

.masthead {
  width: 100%;
  height: 100%;
  background: url('https://picsum.photos/1920/1080.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.masthead h1 {
  font-family: 'rubik', sans-serif;
  font-weight: 500;
  font-size: 4rem;
  color: white;
}

button {
  font-family: 'rubik', sans-serif;
}

.text-shadow {
  text-shadow: 3px 3px 12px #98ba0d, -3px -3px 12px #0d98ba,
    3px -3px 12px #ba0d98;
}
